import React from 'react';
import { Divider, Button, Box, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { FormattedMessage } from 'react-intl';

const RootPage = () => {
  const navigate = useNavigate();
  const currentLang = new URLSearchParams(window.location.search).get('lang') || 'ko';

  const gotoSampleAIPage = () => {
    navigate(`/ai?lang=${currentLang}`);
  };

  const gotoExampleAIPage = () => {
    navigate(`/ex?`);
  };

  const handleChangeLang = (lang: 'ko' | 'en') => {
    const url = new URL(window.location.href);
    url.searchParams.set('lang', lang);
    window.location.href = url.toString(); // 페이지 새로고침 방식
  };

  return (
    <Box
      sx={{
        width: '100%',
        display: 'flex',
        justifyContent: 'center',
        flexDirection: 'column',
        alignItems: 'center',
      }}
    >
      <h1>Hancomdocs Sample Project</h1>
      <Typography variant="h2" sx={{ margin: '0 0 0 20px' }}>
        <FormattedMessage id="ROOT_PAGE" />
      </Typography>
      <Divider sx={{ width: '100%' }} />
      <br />
      <Typography variant="h5">언어 설정</Typography>
      <Box sx={{ display: 'flex', p: 2, border: '1px solid #ddd', borderRadius: 2, backgroundColor: '#f9f9f9' }}>
        <Button variant="contained" onClick={() => handleChangeLang('ko')}>
          한국어 언어 변경
        </Button>
        <Button sx={{ marginLeft: '20px' }} variant="contained" onClick={() => handleChangeLang('en')}>
          English 언어 변경
        </Button>
      </Box>
      <br />
      <Typography variant="h5">샘플 페이지</Typography>
      <Box sx={{ p: 2, border: '1px solid #ddd', borderRadius: 2, backgroundColor: '#f9f9f9' }}>
        <Button
          variant="contained"
          onClick={() => {
            gotoSampleAIPage();
          }}
        >
          <Typography variant="h5">AI 페이지 1 이동</Typography>
        </Button>
      </Box>
      <br />
      <Typography variant="h5">예제 코드 페이지</Typography>
      <Box sx={{ p: 2, border: '1px solid #ddd', borderRadius: 2, backgroundColor: '#f9f9f9' }}>
        <Button
          variant="contained"
          onClick={() => {
            gotoExampleAIPage();
          }}
        >
          <Typography variant="h5">Example 페이지 이동</Typography>
        </Button>
      </Box>
      <br />
      <Typography variant="h5">작업 가이드</Typography>
      <Box sx={{ p: 2, border: '1px solid #ddd', borderRadius: 2, backgroundColor: '#f9f9f9' }}>
        <pre>
          ai 페이지 작업 위치:
          <br />
          /src/view/ai/index.tsx
          <br />
          <br />
          리소스 사용 가이드:
          <br />
          <Box sx={{ p: 1, border: '1px solid #ddd', backgroundColor: '#f9f9f9' }}>
            import &#123; FormattedMessage &#125; from &apos;react-intl&apos;;
            <br />
            <br />
            &lt;FormattedMessage id=&quot;MSG_SAMPLE_RESOURCE&quot; /&gt;
          </Box>
          <br />
          <br />
          다국어 리소스 위치:
          <br />
          한: /src/lang/en.json
          <br />
          영: /src/lang/en.json
          <br />
          <br />
          다국어 상태 확인(디폴트 ko):
          <br />
          한: http://localhost:3000/ai?lang=ko
          <br />
          영: http://localhost:3000/ai?lang=en
          <br />
          <br />
          <Typography variant="h5">더 자세한 가이드는 README.md 파일을 참고 부탁드립니다.</Typography>
          <br />
        </pre>
      </Box>
      <br />
      <Typography variant="h5">다국어 example:</Typography>
      <Box sx={{ p: 2, border: '1px solid #ddd', borderRadius: 2, backgroundColor: '#f9f9f9' }}>
        <Typography variant="h3">
          <FormattedMessage id="MSG_SAMPLE_RESOURCE" />
        </Typography>
      </Box>
      <br />
    </Box>
  );
};

export default RootPage;
